<template>
  <div class="banner">
    <img
      :src="a"
      v-for="(a,index) in bnimg"
      :key="index"
      v-show="n==index"
      @click="ToAdviertisement"
    />
    <div class="banner-circle">
      <ul>
        <li v-for="(a,index) in bnimg" :key="index" :class="n==index?'selected':''"></li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "banner",
  data() {
    return {
      timer: null,
      n: 0
    };
  },
  props: ["bnimg"],
  methods: {
    play() {
      this.timer = setInterval(this.autoPlay, 2000);
    },
    autoPlay() {
      this.n++;
      if (this.n == this.bnimg.length) {
        this.n = 0;
      }
    },
    ToAdviertisement() {
      this.$router.push("/Adviertisement");
    }
  },
  mounted() {
    this.play();
  },
  destroyed() {
    clearInterval(this.timer);
  }
};
</script>
<style scoped>
img {
  width: 100%;
  height: 150px;
}
</style>