import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Find from '../views/Find.vue';
import Order from '../views/Order.vue';
import Mine from '../views/Mine.vue';
import Detail from '../views/Detail.vue'
import Adviertisement from '../views/Adviertisement.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: '/find',
    name: 'Find',
    component: Find
  },
  {
    path: '/order',
    name: 'order',
    component: Order
  },
  {
    path: '/mine',
    name: 'mine',
    component: Mine
  },
  {
    path: '/Detail',
    name: "Detail",
    component: Detail
  },
  {
    path: '/adviertisement',
    name: 'adviertisement',
    component: Adviertisement
  },
];

const router = new VueRouter({
  linkActiveClass: 'active',
  routes
});

export default router;
