<template>
  <div class="home">
    <!-- 导航模块 -->
    <div class="index-category">
      <div class="category" v-for="(list,index) in lists" :key="index">
        <i class="iconfont" :class="list.icon" :style="{background:list.color}"></i>
        <label>{{list.title}}</label>
      </div>
    </div>
    <!-- 轮播模块 -->
    <Banner :bnimg="imgArr" />
    <!-- 列表展示模块 -->
    <div class="index-main" @click="ToDetail()">
      <ul>
        <li class="lists" v-for="(z,index) in Main" :key="index">
          <a>
            <img :src="z.thumbnailUrl" />
          </a>
          <label>
            <b class="discount">{{z.id}}</b>
            <span class="price-text">{{z.id}}</span>
          </label>
        </li>
      </ul>
    </div>
    <div id="one"></div>
  </div>
</template>

<script>
import Banner from "../components/Banner.vue";
export default {
  name: "Home",
  data() {
    return {
      lists: [
        { title: "美食", icon: "icon-xingzhuang", color: "#FF7DB7" },
        { title: "电影", icon: "icon-xingzhuang", color: "#F4BB37" },
        { title: "住宿", icon: "icon-zhusu", color: "#3ACCCF" },
        { title: "娱乐", icon: "icon-yule", color: "#FF7001" },
        { title: "外卖", icon: "icon-waimai", color: "#8F92FE" },
        { title: "打车", icon: "icon-dache", color: "#8BCA5E" },
        { title: "景点", icon: "icon-icon-", color: "#FF6531" },
        { title: "旅游", icon: "icon-lvyou", color: "#8F92FE" }
      ],
      imgArr: [
        // require("../assets/img/banner1.jpg"),
        // require("../assets/img/banner2.jpg"),
        // require("../assets/img/banner3.jpg")
      ],
      MainImg: [],
      Maintitle: [],
      Main: []
    };
  },
  components: {
    Banner
  },
  methods: {
    getList() {
      this.$axios
        .get("http://jsonplaceholder.typicode.com/photos?_limit=5")
        .then(res => {
          res.data.forEach((item, index) => {
            this.imgArr.push(item.thumbnailUrl);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMainImg() {
      this.$axios
        .get("http://jsonplaceholder.typicode.com/photos?_limit=20")
        .then(res => {
          res.data.forEach((items, index) => {
            this.MainImg.push(items.thumbnailUrl);
          });
        });
    },
    getMain() {
      this.$axios
        .get("http://jsonplaceholder.typicode.com/photos?_limit=10")
        .then(res => {
          res.data.forEach((m, index) => {
            this.Main.push(m);
            // console.log("这是第" + index + "条数据");
            // console.log(this.Main);
          });
        });
    },
    ToDetail() {
      this.$router.push("/Detail");
    }
  },
  mounted() {
    this.getList();
    this.getMain();
  }
};
</script>

<style scoped>
#one {
  width: 100%;
  height: 50px;
  clear: both;
}
</style>